.custom-sidebar {
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  width: 300px;
}

.custom-sidebar .header {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  padding: 1rem;
  z-index: 1;
}

.custom-sidebar .content {
  margin-top: 60px; /* Adjust according to header height */
  padding: 1rem;
}

.p-panel-header {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: #fff;
}

.p-panel-content {
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
}
